.skyplus-design-toast {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 1000000 !important;
  top: 10%;
  left: 0;

  &-container {
    font-size: 14px;
    box-sizing: border-box;
    position: relative;
    z-index: 999999;
    color: #000;
    min-width : unset;
    max-width: 70%;
    height: auto;

    @include media('>=tablet') {
      min-width : 400px;
      max-width: 45%;
        }

    padding: 12px;
    border-radius: 12px;
    border: 1px solid #EAF8FF;
    box-shadow: 0 0 28px 16px #4C5D9E0A;
    display: flex;
    justify-content: center;

    &.notifi-variation {
      &--Success {
        border: 1px solid #F0FFF6;

          .icon-close-solid{
            color: #218946;
          }
      }

      &--Warning{
        border: 1px solid #FFF8E5;

        .icon-close-solid{
            color: #A97D0E;
          }
      }

      &--Information{
        border: 1px solid #EAF8FF;

        .icon-close-solid{
            color: #348DC4;
          }
      }

      &--Error{
        border: 1px solid #F4DEDF;

        .icon-close-solid{
            color: #C3272E;
          }

      }
    }

    .desc {
      font-size: 12px;

      @include media('>=tablet') {
        font-size: 14px;
        }

      color: #4B5772;
    }

    .title {
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 5px;
    }

    .skyplus-design-toast {
      &__close {
        float: right;
        cursor: pointer;
        border-radius: 50%;
        // margin-left: rem(10px);
        z-index: 1000000;
        position: relative;

        &::before{
          color: #4B5772;
          font-size: 16px;

          @include media('>=tablet') {
              font-size: 20px;
        }
        }
      }
    }

    .notifi-variation {
      &-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        gap: 8px;
        width: 100%;
      }

      &-icon-wrapper {
        padding-top: 3px;
        font-size: 16px;

        @include media('>=tablet') {
          font-size: 20px;
        }
      }

      &-col {
        width: 85%;
      }
    }


  }

  .top-bottom {
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 10%;
    transition: transform 0.6s ease-in;
    animation: toast-in-bottom 0.7s;
  }

  @keyframes toast-in-bottom {
    from {
      transform: translateY(-100%);
    }

    to {
      transform: translateY(0%);
    }
  }

  .bottom-top {
    bottom: 30%;
    left: 40%;
    transition: transform 0.6s ease-in;
    animation: toast-in-top 0.7s;
  }

  .top-right {
    top: 12px;
    right: 12px;
    transition: transform 0.6s ease-in-out;
    animation: toast-in-right 0.7s;
  }

  .bottom-right {
    bottom: 12px;
    right: 12px;
    transition: transform 0.6s ease-in-out;
    animation: toast-in-right 0.7s;
  }

  .top-left {
    top: 12px;
    left: 12px;
    transition: transform 0.6s ease-in;
    animation: toast-in-left 0.7s;
  }

  .bottom-left {
    bottom: 12px;
    left: 12px;
    transition: transform 0.6s ease-in;
    animation: toast-in-left 0.7s;
  }

  @keyframes toast-in-right {
    from {
      transform: translateX(100%);
    }

    to {
      transform: translateX(0);
    }
  }

  @keyframes toast-in-left {
    from {
      transform: translateX(-100%);
    }

    to {
      transform: translateX(0);
    }
  }

  @keyframes toast-in-top {
    from {
      transform: translateY(0);
    }

    to {
      transform: translateY(-100%);
    }
  }

  .no-animate {
    animation: none;
  }

  .toast-example {
    .skp-iconmoon-icon {
      color: white;
    }
  }
}